
'use client';

import LoadingLink from '@/components/loading-link';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Eye, UserPlus, AlertCircle, Loader2, LogOut as LogOutIcon, LayoutDashboard, UserCheck } from 'lucide-react';
import { useRouter, useSearchParams } from 'next/navigation';
import React, { useEffect, useState, useActionState } from 'react';
import { authenticateUserAction, getSessionUser, type AuthFormState } from '@/lib/actions';
import { useLoading } from '@/contexts/loading-context';
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { useToast } from '@/hooks/use-toast';


export default function LoginPage() {
  const router = useRouter();
  const searchParams = useSearchParams();
  const [isDebugMode, setIsDebugMode] = useState(false);
  const [isLoggedIn, setIsLoggedIn] = useState<boolean | null>(null);
  const [sessionUsername, setSessionUsername] = useState<string>('');
  const { startLoading, stopLoading } = useLoading();
  const [authMode, setAuthMode] = useState<'login' | 'register'>('login');
  const { toast } = useToast();


  const initialState: AuthFormState = { message: null, errors: {}, success: false };
  const [state, formAction, isActionPending] = useActionState(
    async (prevState: AuthFormState, formData: FormData) => {
      startLoading();
      let result;
      try {
        result = await authenticateUserAction(prevState, formData);

        if (result?.success && result.message) {
          // Toast will be shown by useEffect below after successful state update
        } else if (result && (result.errors || (result.message && !result.success))) {
          stopLoading();
        }
        return result;
      } catch (error: any) {
        // This catch is crucial for handling redirect errors from server actions
        if (error && typeof error === 'object' && 'digest' in error && typeof error.digest === 'string' && error.digest.startsWith('NEXT_REDIRECT')) {
          // Re-throw the redirect error so Next.js can handle it
          throw error;
        }
        console.error("Login/Register action failed on client:", error);
        stopLoading(); 
        const errorMessage = error instanceof Error ? error.message : '认证时发生意外错误。';
        return { message: errorMessage, errors: {form: [errorMessage]}, success: false };
      }
    },
    initialState
  );
  
  useEffect(() => {
    if (state?.success && state.message) {
      toast({
        title: authMode === 'login' ? '登录成功！' : '注册成功！',
        description: state.message,
      });
      // After successful login/registration, the server action should handle redirect.
      // If not, or for client-side redirect after toast:
      if (state.userId) { // userId is returned on success by authenticateUserAction
         router.push('/dashboard'); 
         // stopLoading will be called by LoadingProgressBar on new page load
      } else {
        stopLoading(); // Ensure loading stops if no redirect
      }
    } else if (state && !state.success && state.message) { // Handle general form errors from server
        toast({
            title: authMode === 'login' ? '登录失败' : '注册失败',
            description: state.message,
            variant: 'destructive',
        });
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [state?.success, state?.message, state?.userId, authMode, toast, router]);


  useEffect(() => {
    const debugUserIdParam = searchParams.get('debug_user_id');
    setIsDebugMode(debugUserIdParam === 'dream_pep_id' || process.env.NEXT_PUBLIC_DEBUG_MODE === 'true');

    async function checkLoginStatus() {
      try {
        const user = await getSessionUser();
        if (user) {
          setIsLoggedIn(true);
          setSessionUsername(user.username);
        } else {
          setIsLoggedIn(false);
        }
      } catch (e) {
         console.error("登录页面检查登录状态时出错:", e);
         setIsLoggedIn(false);
      }
    }
    checkLoginStatus();
  }, [searchParams]);

  const handleTabChange = (newMode: string) => {
    if (newMode === 'login' || newMode === 'register') {
      setAuthMode(newMode);
    }
  };

  const handleDebugLoginToDashboard = () => {
    startLoading();
    router.push('/dashboard?debug_user_id=dream_pep_id');
  };

  const handleLogout = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    startLoading();
    event.currentTarget.submit(); // Submits to /api/auth/logout
  };


  if (isLoggedIn === null) {
    return (
      <div className="flex justify-center items-center min-h-[calc(100vh-15rem)]">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
      </div>
    );
  }

  const LoginPageContentWrapper = ({ children }: { children: React.ReactNode }) => (
    <div className="flex flex-col items-center justify-center flex-grow w-full bg-gradient-to-br from-background to-secondary/20 py-6">
      {children}
    </div>
  );


  if (isLoggedIn) {
    return (
      <LoginPageContentWrapper>
        <Card className="w-full max-w-md shadow-xl text-center">
          <CardHeader>
            <CardTitle className="text-2xl font-bold">您已登录</CardTitle>
            <CardDescription>您当前以 {sessionUsername} 的身份登录。</CardDescription>
          </CardHeader>
          <CardContent>
            <p>您想退出登录吗？或者前往仪表盘？</p>
          </CardContent>
          <CardFooter className="flex flex-col gap-3">
            <form action="/api/auth/logout" method="POST" onSubmit={handleLogout} className="w-full">
              <Button type="submit" variant="destructive" className="w-full" disabled={isActionPending}>
                <LogOutIcon className="mr-2 h-5 w-5" /> 退出登录
              </Button>
            </form>
            <Button variant="outline" onClick={() => { startLoading(); router.push('/dashboard'); }} className="w-full" disabled={isActionPending}>
              <LayoutDashboard className="mr-2 h-5 w-5" /> 前往仪表盘
            </Button>
          </CardFooter>
        </Card>
      </LoginPageContentWrapper>
    );
  }

  return (
    <LoginPageContentWrapper>
      <div className="w-full max-w-md">
        <Card className="shadow-xl">
          <Tabs value={authMode} onValueChange={handleTabChange} className="w-full">
            <div className="flex justify-center pt-4 mb-4">
              <TabsList>
                <TabsTrigger value="login">登录</TabsTrigger>
                <TabsTrigger value="register">注册</TabsTrigger>
              </TabsList>
            </div>
            
            <TabsContent value="login">
              <CardHeader className="text-center pt-0 pb-2">
                <CardTitle className="text-2xl font-bold">登录 VRCub 活动中心</CardTitle>
                <CardDescription>
                  输入您的 Minecraft 用户名和密码进行登录。
                </CardDescription>
              </CardHeader>
              <form action={formAction}>
                <CardContent className="space-y-4">
                  <input type="hidden" name="authMode" value="login" />
                  <div className="space-y-2">
                    <Label htmlFor="loginMinecraftUsername">Minecraft 用户名</Label>
                    <Input
                      id="loginMinecraftUsername"
                      name="minecraftUsername"
                      type="text"
                      placeholder="例如：Steve"
                      required
                      aria-describedby="login-username-error"
                      disabled={isActionPending}
                    />
                    {state?.errors?.minecraftUsername && authMode === 'login' && <p id="login-username-error" className="text-sm text-destructive">{state.errors.minecraftUsername[0]}</p>}
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="loginPassword">密码</Label>
                    <Input
                      id="loginPassword"
                      name="password"
                      type="password"
                      placeholder="输入您的密码"
                      required
                      aria-describedby="login-password-error"
                      disabled={isActionPending}
                    />
                    {state?.errors?.password && authMode === 'login' && <p id="login-password-error" className="text-sm text-destructive">{state.errors.password[0]}</p>}
                  </div>
                  
                  {state?.message && authMode === 'login' && !state.success && (!state.errors || Object.keys(state.errors).length === 0 || state.errors.form) && (
                    <p className="text-sm text-destructive flex items-center"><AlertCircle className="w-4 h-4 mr-1"/> {state.message || state.errors?.form?.[0]}</p>
                  )}

                  <Button type="submit" className="w-full" disabled={isActionPending}>
                     {isActionPending ? <Loader2 className="mr-2 h-5 w-5 animate-spin" /> : <UserCheck className="mr-2 h-5 w-5"/>}
                    登录
                  </Button>
                </CardContent>
              </form>
            </TabsContent>
            <TabsContent value="register">
              <CardHeader className="text-center pt-0 pb-2">
                <CardTitle className="text-2xl font-bold">注册 VRCub 活动中心账户</CardTitle>
                <CardDescription>
                  使用您的 Minecraft 用户名创建新账户。
                </CardDescription>
              </CardHeader>
              <form action={formAction}>
                <CardContent className="space-y-4">
                  <input type="hidden" name="authMode" value="register" />
                  <div className="space-y-2">
                    <Label htmlFor="registerMinecraftUsername">Minecraft 用户名</Label>
                    <Input
                      id="registerMinecraftUsername"
                      name="minecraftUsername"
                      type="text"
                      placeholder="3-16位字母、数字或下划线"
                      required
                      aria-describedby="register-username-error"
                      disabled={isActionPending}
                    />
                    {state?.errors?.minecraftUsername && authMode === 'register' && <p id="register-username-error" className="text-sm text-destructive">{state.errors.minecraftUsername[0]}</p>}
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="registerPassword">密码</Label>
                    <Input
                      id="registerPassword"
                      name="password"
                      type="password"
                      placeholder="至少6位字符"
                      required
                      aria-describedby="register-password-error"
                      disabled={isActionPending}
                    />
                    {state?.errors?.password && authMode === 'register' && <p id="register-password-error" className="text-sm text-destructive">{state.errors.password[0]}</p>}
                  </div>
                   <div className="space-y-2">
                    <Label htmlFor="confirmPassword">确认密码</Label>
                    <Input
                      id="confirmPassword"
                      name="confirmPassword"
                      type="password"
                      placeholder="再次输入密码"
                      required
                      aria-describedby="confirm-password-error"
                      disabled={isActionPending}
                    />
                    {state?.errors?.confirmPassword && authMode === 'register' && <p id="confirm-password-error" className="text-sm text-destructive">{state.errors.confirmPassword[0]}</p>}
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="qqNumber">QQ号</Label>
                    <Input
                      id="qqNumber"
                      name="qqNumber"
                      type="text" 
                      inputMode="numeric"
                      placeholder="请输入您的QQ号码"
                      required
                      aria-describedby="qq-number-error"
                      disabled={isActionPending}
                    />
                    {state?.errors?.qqNumber && authMode === 'register' && <p id="qq-number-error" className="text-sm text-destructive">{state.errors.qqNumber[0]}</p>}
                  </div>
                  
                  {state?.message && authMode === 'register' && !state.success && (!state.errors || Object.keys(state.errors).length === 0 || state.errors.form) && (
                     <p className="text-sm text-destructive flex items-center"><AlertCircle className="w-4 h-4 mr-1"/> {state.message || state.errors?.form?.[0]}</p>
                  )}

                  <Button type="submit" className="w-full" disabled={isActionPending}>
                    {isActionPending ? <Loader2 className="mr-2 h-5 w-5 animate-spin" /> : <UserPlus className="mr-2 h-5 w-5" />}
                    创建账户
                  </Button>
                </CardContent>
              </form>
            </TabsContent>
          </Tabs>
          <CardFooter className="flex flex-col gap-4 pt-6">
            {isDebugMode && (
              <Button
                type="button"
                variant="secondary"
                className="w-full border-dashed border-primary/50 hover:border-primary"
                onClick={handleDebugLoginToDashboard}
                disabled={isActionPending}
              >
                <Eye className="mr-2 h-5 w-5" />
                调试：以 dream_pep 身份查看仪表盘
              </Button>
            )}
            <p className="text-sm text-muted-foreground">
              遇到问题？请联系<LoadingLink href="#" className="font-medium text-primary hover:underline">管理员</LoadingLink>。
            </p>
          </CardFooter>
        </Card>
      </div>
    </LoginPageContentWrapper>
  );
}

